.contactanos {
    background-image: url(../assets/contactanos/sillones.png);
    height: 355px;
    width: 100%;
}


.contactanos_card {
    width: 357px;
    height: 300px;
    border-radius: 6px;
}

.contactanos_titulo {
    font-size: 32px;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 600;
    margin-top: 25px;
    color: $color-uno;
}

.contactanos_parrafo {
    font-size: 16px;
    margin-top: 31px;
    color: $color-dos;
}

.contactanos_boton {
    width: 164px;
    font-size: 20px;
    margin-top: 31px;
    margin-bottom: 25px;
    color: $color-cuatro;
    background-color: $color-uno;
    outline: none;
    border-radius: 6px;
    padding: 10px 10px 10px 10px;
}